p {
    margin: 0;
}

li {
    list-style: none;
}



.list {
    display: flex;
}

.card {
    margin-right: 15px;
    
}

.profile {
    padding-top: 20px;
    
    width: 300px;
    background-color: #ffffff;
    border-radius: 4px;
    box-shadow: 0px 0px 5px 0px rgba(99, 97, 95, 0.6);
}

.description {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.avatar {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 1px solid #dacfcf;
}

.name {
    margin-top: 15px;

    font-weight: bold;
    font-size: 20px;
}

.tag {
    margin-top: 10px;

    color: #858585;
    font-size: 17px;
}

.location {
    margin-top: 10px;

    color: #858585;
    font-size: 17px;
}

.stats {
    display: flex;
    margin-top: 20px;
    padding: 0;

    border: 1px solid #e7e4e4b7;

    background-color: rgb(233, 233, 233);
}

.stats li {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    width: 100px;
    height: 75px;

    
    border-top: 1px solid rgba(99, 97, 95, 0.2);
}

.stats li:not(:last-child) {
    border-right: 1px solid rgba(99, 97, 95, 0.2);
}

.label {
    color: #858585;
    
}

.quantity {
    margin-top: 5px;
    font-weight: bold;
    font-size: 18px;
}
