.statistics {
    width: 320px;
    margin-left: 40px;
    padding-top: 10px;
    background-color: #ffffff;
    border-radius: 4px;
    box-shadow: 0px 0px 5px 0px rgba(99, 97, 95, 0.6);
}

.title {
    text-align: center;

    color: #6e6e6e;
}

.statList {
    display: flex;
    padding: 0;
}

.item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    width: 80px;
    height: 75px;
    
}

.label {
    margin-bottom: 5px;

    color: #ffffff;
}

.percentage {
    color: #ffffff;
    font-size: 22px;
}
