.friendList {
    margin-top: 50px;
}

.item {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 350px;
    height: 150px;

    margin-bottom: 40px;
    padding: 15px 10px;

    box-shadow: 0px 0px 5px 0px rgba(99, 97, 95, 0.6);
}

.status {
    display: block;
    margin-right: 10px;
    background-color: red;
    width: 25px;
    height: 25px;
    border-radius: 50%;
}

.true {
    background-color: green;
}

/* .false {
    background-color: red;
} */

.avatar {
    margin-right: 15px;
    
    border: 1px solid gray;
    border-radius: 4px;
}




.name {
    font-size: 20px;
    font-weight: bold;
}