.container {
    margin-bottom: 50px;
}

.table {
    margin-right: auto;
    margin-left: auto;
    width: 710px;

    border-radius: 4px;
    
    box-shadow: 0px 0px 5px 0px rgba(99, 97, 95, 0.6);
    
    background-color:transparent;
}

.head {

    background-color: rgb(40, 173, 197);
    color: seashell;
    text-transform: uppercase;
    font-size: 20px;
}

.title {
    padding: 15px 0;
} 

.row {
    color: rgb(80, 78, 78);
    background-color: #fff;
}

.row:nth-child(2n) {
    background-color: rgba(250, 242, 242, 0.884);
}

.cell {
    padding: 10px 0;
    text-align: center;

}